
.home {
    width              : 100%;
    position           : absolute;
    color              : white;
    margin             : 0;
    box-sizing         : border-box;
    background-position: top;
    background-repeat  : no-repeat;
    // background-size    : 100%;
    background-size:100% 100%;
    background-image   : url('./../../assets/home/imxbackground.jpg');
    // // filter: blur(8px);
    // display: flex;

        // .home_img {
        //     position: absolute;
        //     height: 100%;
        //     width: 100%;
        //     object-fit: cover;
        //     z-index: -1;
        // }

    .contain {
        // width:100%;
        padding:53px 26px; 
        margin-left:300px;

        .top {
            background: rgba(58, 58, 58, 0.9);
            border-radius: 8px;
            width : 90%;
            margin:0 auto;

            .top_contain {
                padding : 10px 10px 18px 28px;
                display: flex;
                .top_contain_left {
                    width: 160px;
                    height: 156px;
                    background: black;
                    border-radius :19px;
                    align-self: center;
                    
                    img {
                        width:148px;
                        height:148px;
                        margin:8px 0px 0px 8px;
                    }
                }

                .top_contain_middle {
                    margin-left: 30px;
                }
                .top_contain_right {
                    align-self: end;
                }
            }
        }

        .main {
            width:100%;
            background: rgba(58, 58, 58, 0.9);
            margin-top:35px;
        }

    }

    @media (max-width: 1300px) {
        .contain {
            padding : 21px 46px;
            margin-left: 0px ;
        }

    }

    @media (max-width:862px) {
        .top_contain {
            display: grid !important ;

            .top_contain_middle {
                width : 53%;
            }
            .top_contain_right {
                align-self: end;
                position: absolute;
                right: 7rem;
            }
        }
    }
    @media (max-width:660px) {
        .top_contain_middle {
            width:95% !important;
            margin-left: 0px !important;
        }
        .top_contain_right {
            position: inherit !important;
        }
    }
}

    

