
.home {
    width              : 100%;
    position           : absolute;
    color              : white;
    margin             : 0;
    box-sizing         : border-box;
    background-position: top;
    background-repeat  : no-repeat;
    // background-size    : 100%;
    background-size:100% 100%;
    background-image   : url('./../../assets/home/imxbackground.jpg');
    display: flex;

  .contain {
      width:100%;
      padding:53px 76px; 
      margin-left: 296px;

        .top_title {
          font-size:39px;
          margin : 0;
        }
        .top_context {
          color: #161718;
        }

        .top {
          width:100%;
          background: rgba(58, 58, 58, 0.9);
          border-radius: 8px;

          .top_contain {
            padding:34px; 
            display: flex; 
            justify-content: space-between;


            .top_contain_left {
              display: flex;

              .total {
                margin-left: 10px;

                .first {
                  font-size: 25px;
                }
                .second {
                  color: #777677;
                }
              }
            }
            .top_contain_left img {
              width: 80px;
              height: 106px;
            }
          }
        }

    .main {
        width:100%;
        margin-top:35px;
        display: flex;

        .main_left {
          width: 48%;
          background: rgba(58, 58, 58, 0.9);
          border-radius: 8px;
          height: 100%;

          .main_left_contain {
            padding : 20px;
            
            .title {
              font-size:25px;
            }

            .secret {

              .ids {
                display: flex;
                justify-content: space-between;
              }
              .comma {
                display: flex;
                justify-content: space-between;
                background: rgba(124, 123, 123, 0.9);
                padding :0 12px;
                border-radius: 6px;

              }
              .comma input {
                border:none;
                background: transparent;
                outline: none;
              }
            }
          }
        }

        .main_right {
          width: 48%;
          background: rgba(58, 58, 58, 0.9);
          border-radius: 8px;
          margin-left: 33px;

          .main_right_contain {
            padding : 20px;
            
            .title {
              font-size:25px;
            }

            .secret {

              .ids {
                display: flex;
                justify-content: space-between;
              }
              .comma {
                display: flex;
                justify-content: space-between;
                background: rgba(124, 123, 123, 0.9);
                padding :0 12px;
                border-radius: 6px;

              }
              .comma input {
                border:none;
                background: transparent;
                outline: none;
              }
            }

          }
          .main_right_contain  img {
            margin-top:20px;          }
        }
    }

    .main img {
      cursor: pointer;
    }
    .main img:hover {
      opacity: 0.7;
    }
  }
}

