.main {
  display: flex;
  margin-top:35px;
  flex-wrap: wrap;

    .main_contain {
      width: 265px;
      margin: 0px 15px 30px 0px;
      background: rgba(58, 58, 58, 0.9);
      padding: 10px;
      border-radius: 10px;

        .block {
            background: black;
            border-radius: 10px;
            // width: 100px;
            height: 180px;

        }
        .block img {
            margin: -8px 0px 0px 28px;
            width:200px
        }
        .content1 {
          display: flex;
          justify-content: center;
        }
        p {
            margin : 10px;
          }

        .btn {
          cursor: pointer;
          margin-top: 10px;
        }

        .btn:hover {
          opacity: 0.7;
        }
    }
}
            