.left {
  width:290px;
  background: rgba(58, 58, 58, 0.9);
  height:100%;
  position: fixed;

  @media (max-width: 1300px) {
      display: none;
    }

  .contain {
    padding : 31px 30px 20px 42px;

    .title {
      font-family: Upheaval TT (BRK);
      font-style: normal;
      font-weight: normal;
      font-size: 32px;
      line-height: 37px;
      margin : 0;
      color:white;
    }

    .list {
      margin-top :80px;
      display: grid;
      a {
        color: #777677;
        margin-bottom: 30px;
        cursor: pointer;
        // opacity: 0.6;
        text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
        text-decoration: none;
      }
    }

    .pinkman {
      // margin-top: 120px;
      width:120px;
    }
    .leftbutton {
      width:203px;
      margin-top:-4px;
      cursor:pointer;
    }
    .leftbutton:hover {
      opacity : 0.7;
    }
    p{
      font-family: Upheaval TT (BRK);
      font-style: normal;
      font-weight: normal;
      font-size: 22px;
      line-height: 20px;
      text-align: center;
    }
  }
}


